.Team{
  font-family: 'owo';
}
.menu{
  font-family: 'owo';
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  font-size: 10px;
  color: #f5f5f5;
}
.owo{
  background: #ffffff;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  font-size: 10px;
  color: #f5f5f5;
}

.menubar{
  flex-direction: column;
  z-index: 1;
  position: absolute;
  background-color: #003ab68c;
  width: 100%;
  height: 11%;
  box-shadow: 0 1px 5px #000000;
}

.complete{
  text-shadow: 0 2px 20px #00000050;
  z-index: 1;
  font-size: 18px;
  position: absolute;
  top: 4%;
  right: 80%;
  color: #ffffffaf;
}

.complete:link {text-decoration: none; color: #e6e6e67a;}
.complete:hover {text-decoration: none; color: #ffffffd8;}



.projecting{
  text-shadow: 0 2px 20px #00000050;
  z-index: 1;
  font-size: 18px;
  position: absolute;
  top: 4%;
  right: 20%;
  color: #ffffffaf;
}

.projecting:link {text-decoration: none; color: #e6e6e67a;}
.projecting:hover {text-decoration: none; color: #ffffffd8;}

.outline{
  text-shadow: 0 2px 20px #00000050;
  z-index: 1;
  font-size: 18px;
  position: absolute;
  top: 4%;
  right: 60%;
  color: #ffffffaf;
}

.outline:link {text-decoration: none; color: #e6e6e67a;}
.outline:hover {text-decoration: none; color: #ffffffd8;}


.about{
  text-shadow: 0 2px 20px #00000050;
  z-index: 1;
  font-size: 18px;
  position: absolute;
  top: 4%;
  right: 40%;
  color: #ffffffaf;
}

.about:link {text-decoration: none; color: #e6e6e67a;}
.about:hover {text-decoration: none; color: #ffffffd8;}

@font-face {
  font-family: 'owo';
  src: url('./font/AppleSDGothicNeoB.ttf');
}